<template>
  <div class="example">
    <apexchart width="100%" height="150%" type="bar" :options="chartOptions" :series="series"></apexchart>
  </div>
</template>

<script>
export default {
  name: 'BarExample',
  props: ['title', 'dados', 'color'],
  async created () {
    await this.updateChart()
  },
  data: function () {
    return {
      series: [{
        name: 'Dados',
        data: []
      }],
      chartOptions: {
        chart: {
          stacked: true,
          toolbar: {
            show: true
          },
          zoom: {
            enabled: true
          }
        },
        plotOptions: {
          bar: {
            borderRadius: 20,
            horizontal: false,
            distributed: true
          }
        },
        colors: [],
        xaxis: {
          categories: []
        },
        title: {
          text: '',
          floating: true,
          align: 'center',
          style: {
            fontSize: '16px',
            color: '#616161',
            fontFamily: 'Poppins'
          }
        }
      }
    }
  },
  methods: {
    async updateChart () {
      // this.series = [{
      //   name: 'Dados',
      //   data: []
      // }]

      // this.chartOptions.xaxis.categories = []
      // this.dados.forEach(element => {
      //   this.chartOptions.xaxis.categories.push(element.dados)
      //   this.series[0].data.push(element.valor)
      // })

      const newData = this.dados.map(dado => { return dado.valor })
      const newCategories = this.dados.map(dado => { return dado.dados })
      this.series = [{
        name: 'Valores',
        data: newData
      }]
      this.chartOptions.xaxis.categories = newCategories
      this.chartOptions.colors = this.color
      this.chartOptions.title.text = this.title
    }
  }
}
</script>
