</template>

<script>
import axios from 'axios'
import moment from 'moment'
import BarChart from './charts/BarChart.vue'
export default {
  name: 'DashboardPresenca',
  components: { BarChart },
  data: vm => ({
    urlProd: 'https://neexsa.com.br/api_rh/',
    // urlProd: 'http://localhost:4040/api_rh/',
    menu1: false,
    dataHoje: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    dataPesquisa: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    dateFormatted: vm.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
    mensagem: '',
    snackbar: false,
    colorSnackbar: '',
    loader: false,
    qtdEmAndamento: 0,
    qtdFinalizado: 0,
    qtdNaoIniciado: 0,
    filterColaboradores: [],
    arrayColaboradores: [],
    loadingColaboradores: false,
    loadingTable: false,
    filtroFuncao: [],
    arrayFuncao: [],
    filterObra: [],
    arrayObra: [],
    filterFrenteTrabalho: [],
    arrayFrenteTrabalho: [],
    filterStatusPresenca: [],
    arryStatusPresenca: [],
    filterStatusColab: [],
    arrayStatusColab: [],
    dialogMoreInfo: false,
    itemPresenca: [],
    dataPresenca: null,
    tab: null,
    moreFilter: false,
    graficoColaboradores: [],
    colorColaboradores: [],
    statusColaboradores: [],
    componentKey: 0,
    loadingQtdPresenca: false,
    loadingDashColab: false,
    switchColaboradores: false,
    headersPresenca: [
      {
        text: 'Data',
        align: 'start',
        sortable: false,
        value: 'dataFormated'
      },
      {
        text: 'ID Presença',
        value: 'presenca.idPresenca',
        align: 'center'
      },
      {
        text: 'Obra',
        value: 'presenca.obra',
        align: 'center'
      },
      {
        text: 'Frente Trabalho',
        value: 'presenca.frenteTrabalho',
        align: 'center'
      },
      {
        text: 'Status',
        value: 'presenca.statusPresenca',
        align: 'center'
      },
      {
        text: 'Mais Info.',
        value: 'maisInfo',
        align: 'center'
      }
    ],
    dessertsPresenca: [],

    headersColaborador: [
      {
        text: 'Matricula',
        align: 'start',
        sortable: false,
        value: 'matricula'
      },
      {
        text: 'Nome',
        value: 'nome',
        align: 'center'
      },
      {
        text: 'Função',
        value: 'funcao',
        align: 'center'
      },
      {
        text: 'Obra',
        value: 'obra',
        align: 'center'
      },
      {
        text: 'Status',
        value: 'statusPresenca',
        align: 'center'
      }
    ],
    dessertsColaborador: [],

    headersColabMaisInfo: [
      {
        text: 'Nome',
        value: 'nome',
        align: 'start'
      },
      {
        text: 'Função',
        align: 'center',
        value: 'funcao'
      },
      {
        text: 'Status',
        value: 'statusPresenca',
        align: 'center'
      }
    ],
    dessertsColabMaisInfo: [],
    options: {
      chart: {
        id: 'vuechart-example'
      },
      xaxis: {
        categories: [1991, 1992, 1993, 1994, 1995, 1996, 1997, 1998]
      }
    },
    series: [{
      name: 'series-1',
      data: [30, 40, 45, 50, 49, 60, 70, 91]
    }]
  }),

  async created () {
    await this.getPesquisa()
  },
  computed: {
    computedDateFormatted () {
      return this.formatDate(this.dataPesquisa)
    }
  },
  watch: {
    dataPesquisa (val) {
      this.dateFormatted = this.formatDate(this.dataPesquisa)
    }
  },

  methods: {
    formatDate (data) {
      if (!data) return null

      const [year, month, day] = data.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (data) {
      if (!data) return null

      const [day, month, year] = data.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    async getPesquisa () {
      this.getPresencaDash()
      this.getQtdPresencaDash()
      this.getGraficosDash()
    },
    async getPresencaDash () {
      this.loader = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}presencas-dash`,
        data: {
          dataInicio: moment(this.dataPesquisa).locale('pt-br').set({ hour: 0, minute: 0, second: 0, millisecond: 0 }).valueOf(),
          dataFim: moment(this.dataPesquisa).locale('pt-br').set({ hour: 23, minute: 59, second: 59, millisecond: 0 }).valueOf(),
          filterColaboradores: this.filterColaboradores ? this.filterColaboradores : [],
          filtroFuncao: this.filtroFuncao ? this.filtroFuncao : [],
          filterFrenteTrabalho: this.filterFrenteTrabalho ? this.filterFrenteTrabalho : [],
          filterObra: this.filterObra ? this.filterObra : [],
          filterStatusPresenca: this.filterStatusPresenca ? this.filterStatusPresenca : [],
          filterStatusColab: this.filterStatusColab ? this.filterStatusColab : []
        }
      }
      axios(options)
        .then(res => {
          console.log(res.data)
          const dadosPresenca = res.data.dadosPresenca

          // Filter
          this.arrayFrenteTrabalho = dadosPresenca.map(frente => { return frente.presenca.frenteTrabalho })
          this.arrayObra = dadosPresenca.map(obra => { return obra.presenca.obra })
          this.arryStatusPresenca = dadosPresenca.map(status => { return status.presenca.statusPresenca })

          // Tabela Presença
          this.dessertsPresenca = dadosPresenca.map(x => {
            return {
              ...x,
              dataFormated: moment(new Date(x.presenca.dataPresenca)).format('DD/MM/YYYY')
            }
          })

          // Tabela Colaboradores
          const dadosColaboradores = dadosPresenca.map(colab => { return colab.colaboradores })
          let mergeDadosColaboradores = []
          dadosColaboradores.forEach(element => {
            mergeDadosColaboradores = [...mergeDadosColaboradores, ...element]
          })
          if (this.switchColaboradores) {
            this.getAllColaboradores(mergeDadosColaboradores)
          } else {
            this.dessertsColaborador = mergeDadosColaboradores
          }

          // Filter Colaboradores
          this.arrayColaboradores = mergeDadosColaboradores.map(y => { return y.matricula + ' - ' + y.nome })
          this.arrayFuncao = mergeDadosColaboradores.map(y => { return y.funcao })

          // Filter Colaboradores
          const arrayStatus = mergeDadosColaboradores.filter(y => y.statusPresenca !== '')
          this.arrayStatusColab = arrayStatus.map(y => { return y.statusPresenca })
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar Dados'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loader = false
        })
    },

    async getQtdPresencaDash () {
      this.loadingQtdPresenca = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}qtd-presencas-dash`,
        data: {
          dataInicio: moment(this.dataPesquisa).locale('pt-br').set({ hour: 0, minute: 0, second: 0, millisecond: 0 }).valueOf(),
          dataFim: moment(this.dataPesquisa).locale('pt-br').set({ hour: 23, minute: 59, second: 59, millisecond: 0 }).valueOf(),
          obras: this.filterObra.length > 0 ? this.filterObra : []
        }
      }
      axios(options)
        .then(res => {
          console.log(res.data)
          const parasQtdPresenca = res.data.parasQtdPresenca

          // QTD Em Andamento
          this.qtdEmAndamento = parasQtdPresenca.qtdEmAndamento

          // QTD Finalizado
          this.qtdFinalizado = parasQtdPresenca.qtdFinalizado

          // QTD Não Iniciado
          this.qtdNaoIniciado = parasQtdPresenca.qtdNaoIniciado
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar Dados'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loadingQtdPresenca = false
        })
    },

    async getGraficosDash () {
      this.loadingDashColab = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}graficos-presenca-dash`,
        data: {
          dataInicio: moment(this.dataPesquisa).locale('pt-br').set({ hour: 0, minute: 0, second: 0, millisecond: 0 }).valueOf(),
          dataFim: moment(this.dataPesquisa).locale('pt-br').set({ hour: 23, minute: 59, second: 59, millisecond: 0 }).valueOf()
        }
      }
      axios(options)
        .then(res => {
          this.graficoColaboradores = res.data.graficoColaboradores
          this.colorColaboradores = ['#4CAF50', '#FF9800', '#F44336']
          this.statusColaboradores = res.data.statusColaboradores
          this.componentKey += 1
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar as obras'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loadingDashColab = false
        })
    },

    getAllColaboradores (dados) {
      this.loadingTable = true
      const options = {
        method: 'GET',
        url: `${this.urlProd}dominio/colaboradores-ativos`
      }
      axios(options)
        .then(res => {
          console.log(dados.map(x => { return x.reg }))
          const arrayColab = dados.map(x => { return x.reg })
          const arrayFilter = res.data.filter(y => !arrayColab.includes(y.reg))
          this.dessertsColaborador = arrayFilter.map(w => {
            return {
              ...w,
              statusPresenca: 'Não Iniciado'
            }
          })
          console.log(this.dessertsColaborador)
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar Colaboradores'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loadingTable = false
        })
    },

    /* async getColaboradores () {
      this.loadingColaboradores = true
      if (this.filterColaboradores.length < 1) {
        this.filtroFuncao = ''
      }
      const options = {
        method: 'POST',
        url: `${this.urlProd}filter-colaboradores-dash`,
        data: {
          colaboradores: this.filterColaboradores ? this.filterColaboradores : [],
          funcao: this.filtroFuncao ? this.filtroFuncao : []
        }
      }
      axios(options)
        .then(res => {
          this.arrayColaboradores = res.data.map(y => { return y.matricula + ' - ' + y.nome })
          this.arrayFuncao = res.data.map(y => { return y.funcao })
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar as obras'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loadingColaboradores = false
        })
    }, */

    getMoreInfo (item) {
      console.log(item)
      this.itemPresenca = item.presenca
      this.dessertsColabMaisInfo = item.colaboradores
      this.dataPresenca = item.dataFormated
      this.dialogMoreInfo = true
    },

    getColor (item) {
      if (item === 'Em Andamento') {
        return 'orange'
      } else {
        return 'green'
      }
    },

    getColorColab (item) {
      if (item === 'Presente') {
        return 'green'
      } else if (item === '') {
        return 'transparent'
      } else if (item === 'Não Iniciado') {
        return 'red'
      } else {
        return 'orange'
      }
    }
  }
}
